<script>
import axios from 'axios'
import { colorShiftHsl } from '../../../services/vuestic-ui';
export default {
  name: 'cards',
  data () {
    return {
      parsedData:'',
      sample:'',
      delind:[],
      newstitle: [],
      newscontent: [],
      newsimg: [],
      newslink: [],
      listLoops: 1,
      counter: 1,
      isLoading: false,
    }
  },

  mounted() {
      axios
      .get("https://newsapi.org/v2/top-headlines?country=in&apiKey=YOUR-KEY-HERE")
      .then(response => {
        const products = response.data;
        // this.infoTiles[0].value = products.data.articles;
        this.parsedData=products["articles"]
        // console.log(response.data['articles'])
        this.newstitle= this.parsedData.map(a => a.title)
        this.newscontent= this.parsedData.map(a => a.description)
        this.newscontent.forEach((cont,i) => {
          if(cont!=null){
           if(cont.length <166){
             const diff=166-this.newscontent[i].length
            this.newscontent[i]=this.newscontent[i];
          }
          if(cont.length >166){
            // console.log(cont.length)
            this.newscontent[i]=this.newscontent[i].slice(0,163)+"..."
            cont=cont.slice(0,163)+"..."
          }
         
          }
          })
        this.newslink= this.parsedData.map(a => a.url)
        this.newsimg= this.parsedData.map(a => a.urlToImage)
        // console.log(this.newsimg[0])
        // alert("all done")
        for(var i=0;i<this.newscontent.length;i++){
        if(this.newscontent[i]==null && this.newscontent.length>16){    
        
        // console.log("found",i)
        // this.delind.push(i)
        this.newstitle= this.newstitle.slice(0, i).concat(this.newstitle.slice(i + 1, this.newstitle.length))
        
        
        this.newscontent= this.newscontent.slice(0, i).concat(this.newscontent.slice(i + 1, this.newscontent.length))
        // console.log(this.newscontent)
        this.newslink= this.newslink.slice(0, i).concat(this.newslink.slice(i + 1, this.newslink.length))
        
        this.newsimg= this.newsimg.slice(0, i).concat(this.newsimg.slice(i + 1, this.newsimg.length))
        i=i-1;
        // this.newscontent= this.newscontent.splice(i,1)
        // this.newslink= this.newslink.splice(i,1)
        // this.newsimg= this.newsimg.splice(i,1)
        }
        
       }
// console.log(this.delind) 
// this.newscontent=this.delind.map(i => this.newscontent.splice(i,1))
// console.log(this.newscontent)
  })
  },
  methods: {
    addCards () {
      this.isLoading = true
      setTimeout(() => {
        this.isLoading = false
        ++this.listLoops
      }, 1000)
    },
  },
}
</script>

